import { handleResponse } from '@ab/framework';

var graphql = JSON.stringify({
  query: " query {  allAccounts {    accountNumber    balances {      outstandingBalance      currentBalance    }  }}",
  variables: {}
})

export const getBalance = async (signal) => {
	let requestUrl = `https://accounts-service-gqhl-aka2bqzvrq-em.a.run.app/graphql`;
	try {
		let response = await fetch(requestUrl, {
			signal,
			body: graphql,
			method: "POST",
			headers: {
				"Content-Type": "application/json"
			},
		});
		let result = await handleResponse(response);
		return result.data.allAccounts?.[0]?.balances?.currentBalance;
	} catch (e) {
		throw e;
	}
};

